.InfectionTable {
  --border-color: #ccc;
}

.InfectionTable table {
  border-collapse: collapse;
  border: 1px solid var(--border-color);
}

.InfectionTable thead {
  border-bottom: 1px solid var(--border-color);
}

.InfectionTable td,
.InfectionTable th {
  padding: 10px;
  margin: 0;
}

.InfectionTable tr > *:not(:first-child) {
  border-left: 1px solid var(--border-color);
}

.InfectionTable tr:nth-child(2n) {
  background: #f0f0f0;
}

.InfectionTable .city {
  text-align: left;
}

.InfectionTable .count {
  text-align: center;
  min-width: 65px;
}

.InfectionTable .count > div {
  display: -ms-flexbox;
  display: flex;
}

.InfectionTable .count .sider {
  -ms-flex: 0 1 25%;
      flex: 0 1 25%;
}

.InfectionTable .count .tally {
  -ms-flex: 1 0 50%;
      flex: 1 0 50%;
}

.InfectionTable th.city {
  min-width: 200px;
  display: -ms-flexbox;
  display: flex;
  line-height: 1.4em;
}

.InfectionTable th.city .name {
  -ms-flex: 1 1 100%;
      flex: 1 1 100%;
}

.action-btn {
  border: none;
  background: none;
  font-size: 1.2em;
  cursor: pointer;
}
 
.action-btn:disabled > *  {
  opacity: 0.3;
  position: relative;
}

.action-btn:disabled:hover   {
  cursor: not-allowed;
}

.action-btn:disabled::after {
  border-top: 1px solid black;
  content: "";
  display: block;
  height: 0px;
  position: absolute;
  width: 24px;
  -webkit-transform: translate(-4px, -10px) rotate(-45deg);
      -ms-transform: translate(-4px, -10px) rotate(-45deg);
          transform: translate(-4px, -10px) rotate(-45deg);
  -webkit-transform-origin: center;
      -ms-transform-origin: center;
          transform-origin: center;
  opacity: 1.0;
}


.action-btn.infect {
  color: #080;;
}

.action-btn.epidemic {
  color: #800;
}

.action-btn.infect:hover:not(:disabled) {
  color: #0f0;
}

.action-btn.epidemic:hover:not(:disabled) {
  color: #f00;
}


